.page-footer__wrapper {
  @include wrapper;
  padding-top: 40px;
  padding-bottom: 40px;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }

  @media (min-width: $desktop-width) {
    padding-top: 73px;
  }
}

.page-footer__logo {
  margin-bottom: 39px;
  display: block;
  text-align: center;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    margin: 0;
  }
}

.page-footer__logo-img {
  fill: $blue_grey;
  transition-duration: 0.5s;

  &:hover {
    fill: $basic_pink;
  }

  &:active {
    opacity: 0.3;
    fill: $blue_grey;
  }

  @media (min-width: $desktop-width) {
    width: 147px;
  }
}

.page-footer__logo-img--tablet {
  @media (min-width: $desktop-width) {
    display: none;
  }
}

.page-footer__logo-img--desktop {
  @media (max-width: $tablet-width-only) {
    display: none;
  }
}

.page-footer__social {
  margin-bottom: 40px;

  @media (min-width: $tablet-width) {
    margin: 0;
  }
}
